.slider {
    max-width: 1400px;
    height: 100%;
    margin-left: auto;
    margin-right: auto;

}

.list {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    gap: 20px;
}

.product {
    min-width: 289px;
    height: 100%;
    transition: translate;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
}

.img {
    max-width: 289px;
    max-height: 289px;
    border-radius: 10px;
}

.title {
    font-size: 18px;
    line-height: 150%;
    // text-align: center;
    // justify-content: center;
    // background-color: #626D7D;

    @media (max-width: 800px) {
        font-size: 16px;
        line-height: 150%;
    }
}

.category {
    font-size: 16px;
    line-height: 19px;
    color: #626D7D;
    margin: 9px 0;

    @media (max-width: 800px) {
        font-size: 14px;
        line-height: 17px;
    }
}

.price {
    font-family: "Inter-Bold";
    font-size: 24px;
    line-height: 29px;

    @media (max-width: 800px) {
        font-size: 20px;
        line-height: 24px;
    }
}
