@import "../../../../../assets/colors";


.partner_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  padding: 10px;
  margin-bottom: 20px;
  width: 100%;

  p {
    color: $GRAY_400;
    font-size: 12px;
    margin: 0;
  }
  h6 {
    margin: 0;
  }
}

.buttons_request {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  button {
    margin-right: 10px;
  }
}

.button_accept {
  background-color: $GREEN_500;
}

.button_reject {
  background-color: $RED_500;
}

.confirm {
  color: $GREEN_500;
}

.reject {
  color: $RED_500;
}

.requests_container {
  padding: 10px;
  border-radius: 6px;
  border: solid 2px $BLUE_500;
  background-color: $BLUE_100;

  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 15px 0;
}

.search_input {
  width: 100%;
  //box-sizing: border-box;
  //border: 2px solid #ccc;
  //border-radius: 4px;
  font-size: 16px;
  //background-color: white;
  background-image: url('../../../../../assets/media/icons/search.ico');
  background-size: 20px 20px;
  background-position: 10px 10px;
  background-repeat: no-repeat;
  //padding: 40px;
}

.search_option {
  width: 100%;
}

.search_container {
  margin-bottom: 20px;
}

.search_line {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  button {
    margin-left: 10px;
  }
}