@import "../../../assets/constants";
@import "../../../assets/colors";
@import "../../../assets/fonts";

.chart_container {
  width: 100%;
  //max-height: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.show_labels_container {
  margin-top: 10px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-color: $GRAY_400;
  //border-style: solid;
  padding: 10px;
  //width: 100%;
}

.label_object {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.colored_label_line {
  height: 4px;
  width: 16px;
  border-radius: 2px;
  margin-right: 20px;
  background-color: orange;
}

.loading_div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.label_name {
  font-size: 12px;
  font-family: $openSans;
}

.graph_name {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;

  font-size: 20px;
  font-family: $openSans;
  font-weight: bold;
}