@import "../../../assets/colors";
@import "../../../assets/fonts";


.bt {
  background: #6266EB;
  border-radius: 8px;
  padding: 12px 28px 13px; // 16px 36px 17px
  border: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  align-items: center;
  cursor: pointer;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  @media (max-width:800px) {
      width: 100%;        
  }
}


.button {
  //width: 100%;
  //width: 160px;
  min-width: 80px;
  max-width: 300px;
  //height: 56px;
  //max-width: 300px;
  min-height: 40px;
  //margin: 6px;
  //height: 100%;
  padding: 4px 24px;
  box-shadow: $SHADOW_1;

  background: $PRIMARY;
  //border: solid 2px $BLUE_400;
  border-radius: 6px;

  outline: none;
  color: white;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  font-family: $openSans;
  font-size: 14px;
  //font-weight: 600;
  //font-stretch: ultra-expanded;
  //text-transform: uppercase;
  //font-weight: bold;
  transition: all 0.2s;
  cursor: pointer;
  &:hover {
    color: white;
    background: $BLUE_500;
    //border-color: $GRAY_500;
  }
  &:active {
    color: white;
    background: $GRAY_500;
    //border-color: $GRAY_500;
    //background: $BLUE_300;
    //border-color: $BLUE_300;
    box-shadow: 0 0 3px rgba(0,0,0,0.3);
  }
}

.inverse_button {
  box-shadow: 0 2px 2px rgba(0,0,0,0.2);

  background: white;
  //font-weight: normal;
  border: solid 2px $BLUE_400;
  color: $BLUE_400;

  &:hover {
    background: white;
    color: $BLUE_500;
    border-color: $BLUE_500;
  }
  &:active {
    background: white;
    color: black;
    border-color: black;
    box-shadow: 0 0 3px rgba(0,0,0,0.3);
  }
}

