.box {
    display: flex;
    justify-content: space-between;
}

.menu {
    display: flex;
    grid-gap: 40px;
    font-size: 16px;
    color: #000000;
    align-items: center;
    line-height: 18px;

    @media (max-width: 800px) {
        font-family: "Inter-Medium";
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: fixed;
        left: -120%;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        background-color: #F7F7F9;
        z-index: 10;
        transition: left 1s;
        }
}

.mobile_btn {
    display: none;

    @media (max-width: 800px) {
                display: block;
                position: absolute;
                right: 10px;
                top: 16px;
                cursor: pointer;
                z-index: 10;        
    }
}

@media (max-width: 800px) {
    .active {
        left: 0;
        padding-left: 0;
    }
    
    a {
        font-size: 28px;
    }

}