@import "../../../assets/colors";
@import "../../../assets/fonts";
@import "../../../assets/constants";

.container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  //margin-top: 30px;
  min-width: 400px;
  max-width: 90vw;
  //background-color: #AD009F;
  margin-bottom: 18px;
}

.label_text {
  font-size: 16px;
  //font-weight: bold;
  font-family: 'PT Sans', sans-serif;
}

.left_label {

}

.input {
  width: 100%;
  //max-width: 400px;
  min-width: 200px;
  font-size: 16px;
  font-family: 'PT Sans', sans-serif;

  border: none;
  outline: none;

  background-color: white;

  //background-color: #AD009F;

  //padding: 4px 4px;
  //border-radius: 20px;

  &:focus {
    //border-bottom: solid 2px black;
    //transition-property: border-bottom;
    transition-duration: 0.3s;
  }
}

.active {
  border-color: black;
  color: black;
}

.inactive {
  border-color: $BLUE_500;
  color: $BLUE_500;
}

.error {
  border-color: $RED_500;
  color: $RED_500;
  //background-color: $RED_200;
}

.not_editable {
  border-color: $GRAY_400;
  color: $GRAY_500;
  cursor: default;
}

.field_input {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;

  border-radius: 4px;

  //background-color: green;

  //margin-top: 30px;
  //min-width: 80px;
  //max-width: 500px;
  width: 100%;
  //margin-top: 2px;
  //min-height: 40px;
  border-width: 1px;
  border-style: solid;
  border-left-width: 3px;
  //border-color: $BLUE_500;
  padding: 5px 9px 7px;//4px 4px;
  transition-duration: 0.3s;
  transition-property: all;
  background-color: white;
}

.label {
  //position: absolute;
  text-align: right;
  padding-right: 10px;
  min-width: 110px;
  height: 100%;
  //color: $BLUE_500;

  //top: -24px;
  //left: 10px;
  //background-color: #e26163;
}

.labelError {
  position: absolute;
  font-size: 12px;
  //font-weight: bold;
  font-family: 'PT Sans', sans-serif;
  //color: $BLUE_500;
  bottom: -18px;
  left: 1px;
}

.error_background {
  background-color: $RED_100;
}


@media screen and (max-width: $TABLET_WIDTH) {
  .container {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    min-width: 280px;
  }

  .label {
    //position: absolute;
    text-align: left;
    padding-left: 6px;
    //color: $BLUE_500;

    //top: -24px;
    //left: 10px;
    //background-color: #e26163;
  }
}