@import "../../../../../assets/colors";

.container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  //background-color: #475aff;
  width: 100%;
  height: 100%;

  max-width: 250px;
  min-width: 220px;
  cursor: pointer;
  border-radius: 14px;
  padding: 5px 15px;

  &:hover {
    background-color: $LIGHT_BLUE;
  }
}

.image_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: $GRAY_TEXT_1;
  width: 42px;
  min-width: 42px;
  height: 42px;
  border-radius: 50%;
  overflow: hidden;
}

.image {
  //border-radius: 50%;
  width: 100%;
  height: 100%;
  //background-color: $GRAY_TEXT_1;
}

.build_image {
  //border-radius: 50%;
  width: 30px;
  height: 30px;
}

.titles {
  //width: 100%;
  padding: 6px 2px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-left: 10px;
  //background-color: #e26163;

  h6 {
    font-size: 14px;
    line-height: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    margin: 0;
    padding: 0;
  }
  p {
    font-size: 10px;
    line-height: 12px;
    margin: 0;
    padding: 4px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }
}