

.request_container {
  width: 90%;
  margin: 10px 5%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 16px;
  border-radius: 6px;
  border: solid 1px black;
}

.buttons {
  width: 100%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  button {
    margin-right: 20px;
  }
}