.upload_label {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 80px;
  //max-width: 300px;
  min-height: 40px;
  padding: 4px 16px;
  //margin-right: 20px;
  width: 100%;
  //box-shadow: 0 2px 2px rgba(0,0,0,0.5);

  background: $BLUE_400;
  border-radius: 6px;

  outline: none;
  color: white;

  text-align: center;

  font-family: $openSans;
  font-size: 14px;

  font-weight: bold;
  transition: all 0.2s;
  cursor: pointer;
  &:hover {
    color: white;
    background: $BLUE_500;
    //border-color: $GRAY_500;
  }
  &:active {
    color: white;
    background: $GRAY_500;
    //border-color: $GRAY_500;
    //background: $BLUE_300;
    //border-color: $BLUE_300;
    //box-shadow: 0 0 3px rgba(0,0,0,0.3);
  }
}

.help {
  color: $BLUE_500;
  font-size: 12px;
  padding: 0;
  margin: 10px 20px;
  line-height: 17px;
  min-width: 30vw;
}