.dignities {
    width: 100wv;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.advantаges {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin: 50px 0;

    @media (max-width: 800px) {
        display: grid;
        gap: 10px;
        margin: 40px 0;
        justify-content: space-between;
    }
}

.advantаges-last {

    width: 100wv;
    // background-color: brown;
    @media (max-width: 800px) {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        gap: 20px;
    }
}

.advantages__advantage {
    // width: 392px;
    width: 100%;
    // height: 374px;
    background: #FFFFFF;
    border-radius: 15px;

    @media (max-width: 800px) {
        // width: 320px;
        height: auto;
    }
}

.advantages__advantage-title {
    font-size: 22px;
    line-height: 150%;
    margin: 20px 30px;
    font-family: 'Inter-Bold';

    @media (max-width: 800px) {
        font-size: 18px;
        line-height: 150%;
        margin: 10px 15px;
    }
}

.advantages__advantage-text {
    font-size: 18px;
    line-height: 150%;
    color: #626d7d;
    margin: 0 30px;

    @media (max-width: 800px) {
        font-size: 16px;
        line-height: 150%;
        margin: 0 15px 15px;
    }
}

.advantages__advantage-img {
    width: 100px;
    height: 100px;
    margin-left: 30px;
    margin-top: 30px;

    @media (max-width: 800px) {
        width: 60px;
        height: 60px;
        margin-left: 15px;
        margin-top: 15px;
    }
}

.trophy {
    background: url(../img/svg/trophy.svg);
    background-size: contain
}

.setting {
    background: url(../img/svg/setting.svg);
    background-size: contain
}

.magic {
    background: url(../img/svg/magic.svg);
    background-size: contain
}

.advantаges__statistic {
    // width: 289px;
    width: 100%;
    margin-top: 50px;

    @media (max-width: 800px) {
        margin-top: 30px;
    }
}

.advantаges__statistic-title {
    font-family: "Inter-ExtraBold";
    font-size: 48px;
    line-height: 58px;
    display: flex;
    justify-content: center;
    text-align: center;

    @media (max-width: 800px) {
        font-size: 32px;
        line-height: 39px;
    }
}

.advantаges__statistic-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin-top: 15px;
    display: flex;
    justify-content: center;
    text-align: center;

    @media (max-width: 800px) {
        font-size: 18px;
        line-height: 22px;
        margin-top: 10px;
    }
}

.advantаges__left {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    align-content: center;
    // margin-right: 99px;

    @media (max-width: 800px) {
        margin: 0;
    }
}

.advantages__left-description {
    // width: 416px;
    width: 100%;
    // background-color: #1d5ebe;

    @media (max-width: 800px) {
        width: auto;
    }
}

.advantages__left-description-title {
    font-family: "Inter-Bold";
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;

    @media (max-width: 800px) {
        font-size: 24px;
        line-height: 29px;
    }
}

.advantages__left-description-text {
    font-size: 18px;
    line-height: 150%;
    margin: 25px 0 41px;
    color: #626D7D;

    @media (max-width: 800px) {
        font-size: 16px;
        line-height: 150%;
        margin: 19px 0 20px;
    }
}

.advantаges__right {
    // width: 701px;
    // height: 450px;
    width: 100%;
    max-width: 500px;
    height: auto;
    // min-height: 500px;


    @media (max-width: 800px) {
        width: 322px;
        height: 206px;
        
        // display: flex;
        // flex-direction: column;
        // align-items: center;
        // justify-content: center;
    }
}

.advantаges__right-img {
    background: url(../img/svg/advantages__img.svg);
    background-size: cover;
    width: 100%;
    height: 100%;
}