// @import-normalize;
/* bring in normalize.css styles */

$base-color: #141414;
$black: #000000;

@font-face {
    font-family: "Inter-Bold";
    src: url(../fonts/Inter-Bold.ttf);
    font-weight: 700;
}

@font-face {
    font-family: "Inter-ExtraBold";
    src: url(../fonts/Inter-ExtraBold.ttf);
    font-weight: 800;
}

@font-face {
    font-family: "Inter-Medium";
    src: url(../fonts/Inter-Medium.ttf);
    font-weight: 500;
}

@font-face {
    font-family: "Inter-Regular";
    src: url(../fonts/Inter-Regular.ttf);
    font-weight: 400;
}

@font-face {
    font-family: "Inter-SemiBold";
    src: url(../fonts/Inter-SemiBold.ttf);
    font-weight: 600;
}

a {
    text-decoration: none;
}

.App {
    width: 96%;
    max-width: 96%;
    margin-left: 2%;
    margin-right: 2%;
    // margin: 0;
    // background-color: #6266EB;
    // display: none;
}

body {
    font-family: "Inter-Regular";
    font-size: 18px;
    font-weight: 400;
    color: $base-color;
    line-height: 150%;
    margin: 0 auto;
    padding: 0;
    background: #F7F7F9;
}

a {
    cursor: pointer;
    color: $black;
}

