.svg {
    display: block;
    background-position: center;
    background-repeat: no-repeat;
}

.container {

}

.main {
    @media (max-width: 800px) {
        padding-left: 10px;
        padding-right: 10px;
        // width: 320px;
        margin-left: auto;
        margin-right: auto;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    @media (min-width: 800px) {
        padding-left: 10px;
        padding-right: 10px;
        // width: 90vw;
        // width: 320px;
        margin-left: auto;
        margin-right: auto;
    }
}
.header {
    @media (max-width: 800px) {
        padding-left: 10px;
        padding-right: 10px;
        // width: 320px;
        margin-left: auto;
        margin-right: auto;
    }
}

.header__top {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-around;
    padding: 19px 100px;

    @media (max-width: 800px) {
        padding: 0 0 0 9px;
        justify-content: space-between;
        border-bottom: 1px solid #DEE1E5;
        height: 56px;
    }
}

.logo {
    display: flex;
    grid-gap: 8.34px;
    align-items: center;
}

.logo__svg {
    background-image: url(../img/svg/logo.svg);
    width: 60.33px;
    background-size: contain;
    height: 23.71px;
}

.logo-text__svg {
    background-image: url(../img/svg/logo_text.svg);
    width: 59.3px;
    background-size: contain;
    height: 23.2px;
}

.header__main {
    display: flex;
    justify-content: center;

    @media (max-width: 800px) {
        display: flex;
        flex-direction: column-reverse;
    }
}

.header__main-left{
    display: grid;
    align-items: center;
    width: 592px;

    @media (max-width: 800px) {
        display: block;
        width: auto;
    }
}

.header__main-left-title{
    font-family: "Inter-Bold";
    font-size: 40px;
    line-height: 48px;

    @media (max-width: 800px) {
        font-size: 26px;
        line-height: 31px;
    }
}

.header__main-left-subtext{
    font-size: 20px;
    line-height: 150%;
    margin: 25px 0;
    width: 456px;

    @media (max-width: 800px) {
        width: auto;
        font-size: 16px;
        line-height: 150%;
        margin: 15px 0;
    }
}

.header__main-right{
    background-image: url(../img/svg/header-right.svg);
    width: 647px;
    height: 453px;
    display: flex;
    align-items: flex-end;
    background-size: contain;

    @media (max-width: 800px){
        background-image: url(../img/svg/header-right-mob.svg);
        // width: 320px;
        // height: 224px;
    }
}

.header__main-right-query {
    display: inline-flex;
    align-items: center;
    padding: 15.71px;
    background: #FFFFFF;
    box-shadow: 0px 25.1324px 25.1324px -12.5662px rgb(0 0 0 / 20%);
    border-radius: 11.7808px;
    justify-content: flex-start;
    margin-bottom: 44px;
    margin-left: 20px;

    @media (max-width: 800px) {
        font-weight: 500;
        font-size: 6.21513px;
        line-height: 8px;
        font-family: "Inter-Medium";
        margin-bottom: 23px;
        margin-left: 10px;
        padding: 7.77px;
    }
}

.header__main-right-prompt {
    display: flex;
    gap: 31.31px;
    margin-top: 21.13px;

    @media (max-width: 800px) {
        margin-top: 9.87px;
        gap: 15.55px;
    }
}

.header__main-right-requests {
    font-family: "Inter-Medium";
    font-size: 12.5662px;
    line-height: 15px;
    display: flex;
    gap: 7.85px;

    @media (max-width: 800px) {
        font-size: 6.21513px;
        line-height: 8px;
    }
}

.header__main-right-requests-num {
    font-family: "Inter-Bold";
    font-size: 9.42466px;
    line-height: 11px;
    color: #FFFFFF;
    padding: 1.57078px 7.85388px;
    background: #6266EB;
    border-radius: 24.347px;

    @media (max-width: 800px) {
        font-size: 4.66135px;
        line-height: 6px;
        padding: 0.78px 3.88px;
    }
}

.header__main-right-num {
    font-family: "Inter-Bold";
    font-size: 32.9863px;
    line-height: 40px;
    
    @media (max-width: 800px) {
        font-family: "Inter-Bold";
        font-size: 16.3147px;
        line-height: 20px;
    }
}

.header__main-right-text {
    font-size: 9.42466px;
    line-height: 11px;
    color: #7F8A9B;

    @media (max-width: 800px) {
        font-size: 4.66px;
        line-height: 6px;
    }
}

.header__main-right-cirсle {
    width: 91.89px;
    height: 91.89px;
    margin-left: 58.38px;

    @media (max-width: 800px) {
        width: 45.45px;
        height: 45.45px;
        margin-left: 28.61px;
    }
}

.modal {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
}

.modal.active {
    transform: scale(1);
}

.CircularProgressbar .CircularProgressbar-text {
    font-family: "Inter-Bold";
    font-weight: 700;
    font-size: 18.8493px;
    line-height: 23px;

    @media (max-width: 800px) {
        font-size: 9.32269px;
        line-height: 11px;
    }
}

.header__main-right-subtext {
    color: #7F8A9B;
    font-size: 9.42466px;
    line-height: 11px;
    margin-top: -40px;

    @media (max-width: 800px){
        font-size: 4.66135px;
        line-height: 6px;
        margin-top: -20px;
    }
}

.header__main-right-sub {
    display: grid;
    justify-content: center;
}