$margin_content_left: 250px;
$second_menu_color: rgba(255,255,255,0.5);


.choose_stat_type_block {
  position: fixed;
  width: $margin_content_left;
  top: $header_height;
  bottom: 0;
  left: 0;
  padding-right: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: $second_menu_color;//rgba(0, 0, 0, 0);
  //background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);

  box-shadow: 0 0 10px rgba(0,0,0,0.1);

  z-index: 10000;
}

.choose_stat_type {
  height: 100%;
  width: 100%;
  overflow: auto;
  //overflow-scrolling: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: rgba(0, 0, 0, 0);
  //z-index: 10000;
}

.choose_stat_type::-webkit-scrollbar {
  width: 7px;
  background-color: rgba(0, 0, 0, 0);
}
.choose_stat_type::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}
//.choose_stat_type-webkit-scrollbar-track {
//  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
//  background-color: #f9f9fd;
//}

$margin_stat_elem_hor: 4%;
$padding_stat_elem: 10%;

.stat_elem {
  width: 100% - $padding_stat_elem;
  min-height: 45px;
  //height: 200px;
  margin: 1.5% 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0);
  padding-left: $padding_stat_elem;

  font-weight: bold;
  font-family: $openSans;
  font-size: 16px;

  transition: all 0.2s;
  cursor: pointer;

  &:hover {
    //box-shadow: 0 3px 5px rgba(0, 0, 0, 0.5);
    background-color: rgba(0, 0, 0, 0.03);
    //color: $ORANGE_500;
  }
}

.active_stat_elem {
  color: $BLUE_500;
  border-radius: 0 20px 20px 0;
  background-color: rgba(74, 87, 255, 0.08);

  &:hover {
    background-color: rgba(74, 87, 255, 0.08);
  }
}

$margin_content_top: 60px;

.stat_header {
  position: fixed;
  top: $header_height;
  height: 100%;
  min-height: $margin_content_top;
  max-height: $margin_content_top;
  left: $margin_content_left;// + 5px;
  padding-left: 20px;
  padding-right: 20px;
  //padding-top: 10px;
  right: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: $second_menu_color;//$GRAY_50;//rgba(0,0,0,0.5);
  //background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  z-index: 10000;

  box-shadow: 10px 0 10px rgba(0,0,0,0.1);

  border-bottom: solid 1px $GRAY_300;
}

.stat_time {
  //background-color: #58FFB8;
