@import "../../../assets/colors";

.screen {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  width: 100%;
  //background-color: #e26163;
}

.main_content {
  width: 100%;
  //background-color: #58FFB8;
  padding: 0 10px;
}

.header {
  padding: 0 10px;
}

.block_menu {
  padding: 10px 0;
  max-width: 15vw;
  min-width: 200px;
}

.menu_name_div {
  padding: 0 14px;
  border-bottom: solid 1px $GRAY_300;
  margin-bottom: 14px;
  //mar-left: 14px;
  //margin-right: 14px;
  //padding-bottom: 0;
  width: 90%;
  //background-color: #58FFB8;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  p {
    font-size: 14px;
    padding: 0;
    margin: 0;
    //font-weight: bold;
    //background-color: #e26163;
  }
  h6 {
    font-size: 16px;
    padding: 0;
    margin: 0;
  }
}

.menu_item {
  width: 100%;
  cursor: pointer;
  padding: 0 14px;
  height: 36px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  border-left: solid 3px white;

  &:hover {
    background: $BLUE_100;
    border-left: solid 3px $BLUE_100;
  }

  p {
    width: 90%;
    text-align: left;
    font-size: 14px;
    //font-weight: bold;
  }
}

.active_menu_item {
  //padding-left: 14px;
  border-left: solid 3px $BLUE_500;
  background: $BLUE_100;

  p {
    font-weight: bold;
  }

  &:hover {
    //background: $BLUE_100;
    border-left: solid 3px $BLUE_500;
  }
}

.right_notification {
  border-radius: 50%;
  //padding: 4px;
  height: 20px;
  font-size: 10px;
  background-color: $RED_500;
  color: white;
  text-align: center;
  margin: 0;
  padding: 0;
  width: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
