@import "../../../../assets/colors";

.wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  //background-color: #475aff;
  padding-right: 20px;
}

.bell {
  width: 26px;
  height: 26px;
  margin-right: 20px;
}

.image {
  background-color: $GRAY_TEXT_3;
  border-radius: 50%;
  width: 42px;
  min-width: 42px;
  height: 42px;

  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;

  p {
    color: white;
    padding: 0;
    margin: 0;
  }
}

.user_name_container {
  //width: 100%;
  padding: 6px 2px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-left: 10px;
  //background-color: #e26163;

  h6 {
    font-size: 14px;
    line-height: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    margin: 0;
    padding: 0;
  }
  p {
    font-size: 10px;
    line-height: 12px;
    margin: 0;
    padding: 4px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }
}