@import "../../../assets/colors";
@import "../../../assets/fonts";
@import "../../../assets/constants";


.label2 {
  position: absolute;
  font-size: 12px;
  line-height: 15px;
  color: #7F8A9B;
  margin-left: 14px;
  margin-top: 12px;
}

.active_content {
  position: relative;
  display: block;
}

.col25 {
  float: left;
  width: 25%;
  margin-top: 6px;
}

.col75 {
  float: left;
  width: 75%;
  margin-top: 6px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  //background-color: #58FFB8;
}

//.row_checkbox label:before {
//  border-radius: 3px;
//}

.row_checkbox {
  width: 100%;
  padding: 6px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.label_checkbox {
  text-align: left;
  padding-left: 10px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.row {
  //background-color: #8ea1f1;
  width: 100%;
  padding: 6px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  p {
    color: $RED_500;
    font-size: 14px;
  }

  p::before {
    display: inline;
    content: "⚠ ";
  }
}

.row .col25 .col75 {
  box-sizing: border-box;
}

.container {
  // display: block;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  // max-width: 350px;
  // background-color: #7F8A9B;
  width: 100%;
}

.error_wrapper {
  margin-left: 5%;
  width: 95%;
  //background-color: #AD009F;
  p {
    color: $RED_500;
    font-size: 12px;
  }

  p::before {
    display: inline;
    content: "⚠ ";
  }
}

/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 768px) {
  .col25, .col75 {
    width: 100%;
    margin-top: 0;
  }
  .row {
    flex-direction: column;
    align-items: flex-start;
  }
}