
.content {
    background: #FFFFFF;
    border-radius: 15px;
    padding: 30px;
    min-width: 320px;
    max-width: 490px;
    height: auto;
    display: grid;
}

.title {
    font-family: "Inter-Bold";
    font-size: 20px;
    line-height: 24px;
}

.bt {
    background: #6266EB;
    border-radius: 8px;
    padding: 16px 109.5px 17px;
    border: none;
    font-weight: 500;
    font-size: 16px;
    color: #FFFFFF;
    align-items: center;
    cursor: pointer;
    margin-top: 16px;
}

.footer {
    display: grid;
    margin-top: 20px;
    justify-items: center;
}
.social {
    font-size: 14px;
    line-height: 17px;
    color: #626D7D;
}

.icons {
    display: flex;
    gap: 26.5px;
    padding: 0;
    margin-bottom: 0;
}

.icon {
    width: 40px;
    height: 40px;
    box-shadow: 0px 0 18px 1px rgb(0 0 0 / 20%);
    border-radius: 8px;
    cursor: pointer;
}

.vk {
    background: url(../img/svg/vk.svg);
}

.google {
    background: url(../img/svg/google.svg);
}

.ya {
    background: url(../img/svg/ya.svg);
}