.supply {
    display: flex;
    align-content: center;
    justify-content: center;
    gap: 20px;

    @media (max-width: 800px) {
        gap: 10px;
        flex-direction: column;
        display: flex;
        align-content: center;
        justify-content: center;

        // width: 100vw;
        // background-color: antiquewhite;
    }
}

.supply__object {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 30px;
    gap: 29px;
    width: 100%;
    // width: 538px;
    background: #ffffff;
    border-radius: 25px;

    @media (max-width: 800px) {
        gap: 21px;
        margin: 0 5vw;
        width: 90vw;
        // width: auto;

        padding: 20px 15px;
    }
}

.supply__object-top {
    display: grid;
    gap: 29px;

    @media (max-width: 800px) {
        display: flex;
        gap: 15px;
    }
}

.supply__object-img {
    width: 90px;
    height: 90px;
    background-size: contain;

    @media (max-width: 800px) {
        width: 60px;
        height: 60px;
    }
}

.supply__object-img-wellet {
    background: url(../img/svg/wallet.svg);
}

.supply__object-img-bag {
    background: url(../img/svg/bag.svg);
}

.supply__object-title {
    font-family: "Inter-Bold";
    font-size: 32px;
    line-height: 39px;

    @media (max-width: 800px) {
        font-size: 20px;
        line-height: 24px;
    }
}

.suply__object-subtitle {
    color: #6266EB;
}

.supply__ul {
    font-family: "Inter-Medium";
    list-style: none;
    font-size: 18px;
    line-height: 150%;
    // margin-left: 48px;
    // background-color: #6266EB;

    @media (max-width: 800px) {
        font-size: 14px;
        line-height: 150%;
    }
}

.supply__ul li {
    margin: 20px 0;
    margin-left: 48px;
}

.supply__ul li:before {
    content: ' ';
    position: absolute;
    background: url(../img/svg/li.svg);
    height: 24px;
    width: 24px;
    margin-left: -40px;
}

.content__title {
    font-family: "Inter-Bold";
    margin: 100px 0 50px;
    display: flex;
    justify-content: center;
    gap: 20px;

    @media (max-width: 800px) {
        margin: 50px 0 20px;
        line-height: 29px;
    }
}

.empty {
    width: 598px;

    @media (max-width: 800px) {
        display: none;
    }
}

.content__categories {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    max-width: 1250px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 100px;

    @media (max-width: 800px) {
        width: 100vw;
        padding: 20px;
        overflow-x: scroll;
        flex-wrap: nowrap;
        justify-content: flex-start;
        margin: 0;
    }
}

.content__categories-category {
    display: flex;
    justify-content: center;
    align-items: center;
    // width: 392px;
    width: 45%;
    height: 180px;
    background: #ffffff;
    border-radius: 15px;

    @media (max-width: 800px) {
        flex-direction: column-reverse;
        height: auto;
        align-items: flex-start;
        padding: 14px 19px 15px;
    }
}

.content__categories-category-description {
    width: 202px;
    display: grid;
    gap: 29px;

    @media (max-width: 800px) {
        gap: 10px;
        margin-top: 10px;
        margin-bottom: 15px;
    }
}

.content__categories-category-description-title {
    font-family: "Inter-SemiBold";
    font-size: 24px;
    line-height: 150%;

    @media (max-width: 800px) {
        font-size: 18px;
        line-height: 150%;
    }
}

.content__categories-category-description-text {
    font-family: "Inter-Medium";
    font-size: 16px;
    line-height: 150%;
    color: #6266EB;
}

.content__categories-category-img {
    width: 130px;
    height: 130px;
}

.content__categories-category-img-electric {
    background: url(../img/svg/electric.svg);
}

.content__categories-category-img-wheel {
    background: url(../img/svg/wheel.svg);
}

.content__categories-category-img-machine {
    background: url(../img/svg/machine.svg);
}

.content__categories-category-img-medicine {
    background: url(../img/svg/medecine.svg);
}

.content__categories-category-img-packaging {
    background: url(../img/svg/packaging.svg);
}

.content__categories-category-description-last {
    display: grid;
    justify-items: center;
    align-items: center;
}

.content__categories-category-description-last-title {
    font-family: "Inter-SemiBold";
    font-size: 54px;
    line-height: 65px;
    color: #000000;
}

.content__categories-category-description-last-text {
    font-size: 18px;
    line-height: 150%;
    color: #000000;
}