@import "../../../assets/colors";

.container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.company {
  width: 80%;
  padding: 5px 20px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;

  margin-bottom: 14px;

  &:hover {
    color: white;
    background: $BLUE_100;
    //border-color: $GRAY_500;
  }
}

.no_companies_div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;

  margin-top: 20px;

  p {
    text-align: center;
    margin: 14px 20px;
  }
}