@import "../../assets/constants";
@import "../../assets/colors";
@import "../../assets/fonts";

$margin_content_left: 250px;

.choose_stat_type_block {
  position: fixed;
  width: $margin_content_left;
  top: $header_height;
  bottom: 0;
  left: 0;
  padding-right: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;//rgba(0, 0, 0, 0);
  //background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);

  box-shadow: 0 0 10px rgba(0,0,0,0.1);

  z-index: 10000;
}

.choose_stat_type {
  height: 100%;
  width: 100%;
  overflow: auto;
  //overflow-scrolling: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: rgba(0, 0, 0, 0);
  z-index: 10000;
}

.choose_stat_type::-webkit-scrollbar {
  width: 7px;
  background-color: rgba(0, 0, 0, 0);
}
.choose_stat_type::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}
//.choose_stat_type-webkit-scrollbar-track {
//  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
//  background-color: #f9f9fd;
//}

$margin_stat_elem_hor: 4%;
$padding_stat_elem: 10%;

.stat_elem {
  width: 100% - $padding_stat_elem;
  min-height: 45px;
  //height: 200px;
  margin: 1.5% 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0);
  padding-left: $padding_stat_elem;

  font-weight: bold;
  font-family: $openSans;
  font-size: 16px;

  transition: all 0.2s;
  cursor: pointer;

  &:hover {
    //box-shadow: 0 3px 5px rgba(0, 0, 0, 0.5);
    background-color: rgba(0, 0, 0, 0.03);
    //color: $ORANGE_500;
  }
}

.active_stat_elem {
  color: $BLUE_500;
  border-radius: 0 20px 20px 0;
  background-color: rgba(74, 87, 255, 0.08);

  &:hover {
    background-color: rgba(74, 87, 255, 0.08);
  }
}

$margin_content_top: 60px;

.stat_header {
  position: fixed;
  top: $header_height;
  height: 100%;
  min-height: $margin_content_top;
  max-height: $margin_content_top;
  left: $margin_content_left + 5px;
  padding-left: 20px;
  padding-right: 20px;
  //padding-top: 10px;
  right: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: white;//$GRAY_50;//rgba(0,0,0,0.5);
  //background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  z-index: 10000;

  box-shadow: 10px 0 10px rgba(0,0,0,0.1);
}

.stat_time {
  //background-color: #58FFB8;
  font-size: 18px;
  font-family: $openSans;
}

.stat_header_section_name {
  font-size: 24px;
  font-family: $openSans;
  font-weight: bold;
  //color: white;
}

.main_content {
  left: $margin_content_left;
  padding-left: 30px;
  top: $margin_content_top + $header_height;
  background-color: $GRAY_50;//white;
  right: 0;
  bottom: 0;
  position: fixed;
  overflow: auto;
}

.main_content::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  background-color: rgba(0, 0, 0, 0.0);
}

.main_content::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.3);
}

.graphics_container {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.graphic_elem {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.graphics_line {
  display: flex;
  width: 95%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.div_loading {
  width: 90%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.rating_products_div {
  min-height: 100vh;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  //background-color: #58FFB8;
  padding: 20px;
}

.rating_elem_div {
  margin-top: 10px;
  min-height: 50px;
  width: 100%;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  //background-color: #FFB49C;
}

.rating_text {
  font-family: $openSans;
  font-size: 20px;
  font-weight: bold;
  padding-left: 5px;
}

.rating_number {
  display: flex;
  flex: 0.08;
  align-items: center;
  justify-content: center;
  //background-color: #C8D4F2;
  background-color: black;
  border-radius: 30px;
  color: white;
}

.rating_name {
  display: flex;
  flex: 0.56;
  align-items: center;
  justify-content: flex-start;
  //background-color: #346ffd;
  text-align: left;
}

.rating_value {
  display: flex;
  flex: 0.3;
  align-items: center;
  justify-content: center;
  //background-color: #ff6acb;
}

.full_white_screen {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  //z-index: -100;
  background-color: $GRAY_50;
}

.conversion_div {
  width: 90%;
  min-height: 70px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  background-color: white;
  border-radius: 20px;
  border-style: solid;
  border-width: 1px;
  border-color: $BLUE_400;
  z-index: 1000;
  margin-bottom: 10px;
}

.head_div {
  width: 100%;
  margin-bottom: 10px;
  font-family: $openSans;
  font-size: 16px;
  font-weight: bold;
}

.activity_div {
  width: 90%;
  padding: 8px 24px;
  min-height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-radius: 20px;
  border-style: solid;
  border-width: 1px;
  border-color: $BLUE_400;
  margin-bottom: 10px;
}

.activity_inner_div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}

.activity_inner_elem {
  min-height: 40px;
  min-width: 100px;
  max-width: 200px;
  width: 100%;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 5px;
  border-radius: 10px;
  border-color: $GRAY_50;
  border-style: solid;
  border-width: 1px;
  font-size: 14px;
  font-family: $openSans;

  transition: all 0.5s;

  &:hover {
    background-color: rgba(0, 0, 255, 0.1);
  }
}

.activity_inner_elem_active {
  background-color: rgba(0, 0, 255, 0.1);
}

.activity_inner_elem_disable {
  cursor: default;
  color: $GRAY_400;
  background-color: $GRAY_200;
  &:hover {
    background-color: $GRAY_200;
  }
}

.text_div {
  font-family: $openSans;
  font-size: 16px;
}

.conversion_settings_div {
  margin-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}