.works {
    display: grid;
    justify-content: center;
}

.works__title {
    display: grid;
    justify-content: center;
    font-family: "Inter-Bold";
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;
    margin-bottom: 90px;
}

.works__footer {
    display: flex;
    justify-content: center;
    margin-top: 76px;
    margin-bottom: 100px;

    @media (max-width: 800px) {
        display: none;
    }
}

.works__points {
    @media (max-width: 800px) {
        display: none;
    }
}

.works__points-mob {
    display: none;

    @media (max-width: 800px) {
        display: flex;
        overflow-x: scroll;
        margin-bottom: 40px;
    }
}

.works__points-point {
    display: flex;
    justify-content: center;

    @media (max-width: 800px){
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        min-width: 240px;
    }
}

.works__points-point-left {
    display: grid;
    align-content: center;
    // width: 465px;
    width: 40%;

    @media (max-width: 800px) {
        width: auto;
    }
}

.works__points-point-left-title {
    font-family: "Inter-Bold";
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 15px;

    @media (max-width: 800px) {
        font-size: 18px;
        line-height: 22px;
    }
}

.works__points-point-left-text {
    font-size: 18px;
    line-height: 150%;
    color: #626D7D;

    @media (max-width: 800px) {
        font-size: 14px;
        line-height: 150%;
    }
}

.works__points-point-right {
    // width: 465px;
    // width: 40wv;
    width: 40%;

    @media (max-width: 800px) {
        width: auto;
    }
}

.works__points-point-right-left{
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
}

.works__points-point-right-number {
    font-family: "Inter-Bold";
    font-size: 64px;
    line-height: 77px;
    color: #DEE1E5;

    @media (max-width: 800px) {
        font-size: 48px;
        line-height: 58px;
        
    }
}

.works__points-point-center {
    display: grid;
    align-content: center;
    height: 174.8px;
    border: 1px solid #6266EB;
    margin: 0 112px 0 114px;

    @media (max-width: 800px){
        margin: 0;
        height: 0;
        min-width: 240px;
    }
}


.works__points-point-center:before, .works__points-point-center-end:after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    background: #6266EB;
    border-radius: 50%;
    margin-left: -5px;
    margin-top: -5px;

    @media (max-width: 800px) {
        display: none;
    }
}

.works__points-point-center-end:after {
    margin-top: 166px;
}