.tutorial {
    margin-top: 100px;
}

.question_wrapper {
    // width: 100vw;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    // background-color: #6266EB;
    @media (max-width: 800px) {
        width: 100vw;
    }
}

.caption {
    display: grid;
    text-align: center;

    justify-content: center;
    font-family: "Inter-Bold";
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;
    margin-bottom: 50px;

    @media (max-width: 800px) {
        font-size: 24px;
        line-height: 29px;
    }
}

.question {
    // width: 1156px;
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    background: #ffffff;
    border-radius: 15px;
    padding: 30px;
    margin-bottom: 20px;
    cursor: pointer;
    
    @media (max-width: 800px) {
        width: 90%;
        padding: 15px;
    }
}

.title {
    width: 100%;
    font-size: 18px;
    font-family: "Inter-SemiBold";
    line-height: 22px;
    display: flex;
    justify-content: space-between;
    // text-align: center;
    // background-color: aqua;

    @media (max-width: 800px) {
        font-size: 16px;
        line-height: 19px;
    }
}

.description {
    font-size: 18px;
    line-height: 150%;
    margin-top: 20px;
    color: #626D7D;

    @media (max-width: 800px) {
        font-size: 14px;
        line-height: 150%;
    }
}

.activ {
    transform: rotate(90deg);
    color: #6266EB;
}

.disabled {
    transform: rotate(270deg);
}