@import "../../../../../assets/colors";

.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-width: 190px;

  a {
    padding: 6px 0;
  }

  button {
    margin: 0;
  }
  //padding: 10px;
  //margin-bottom: 10px;

  //border-radius: 6px;
  //background-color: rgba(190, 190, 190, 0.5);
  //
  //button {
  //  margin-right: 10px;
  //}
}

.products_container {
  display: grid;
  //grid-template-columns: repeat(2, 1fr);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: minmax(150px, auto);
}

.default_image {
  width: 100%;
  height: 100%;
  border-radius: 6px;
  border: dashed 2px $BLUE_200;
  background-color: $BLUE_100;
}

.image_container {
  width: 100px;
  //max-width: 90vw;
  height: 100px;
  //max-height: 90vw;

  overflow: hidden;
  border-radius: 6px;
  margin: 6px;
  margin-right: 20px;

  display: flex;
  align-items: center;
  justify-content: center;
  //margin-bottom: 10px;
}
.name_container {
  //display: flex;
  //flex-direction: column;
  //align-items: flex-start;
  //align-self: center;
  //justify-content: flex-start;
  //height: 20%;
  //background-color: #58FFB8;
  min-width: 200px;
  padding-left: 10px;

  p {
    font-size: 14px;
    margin-top: 6px;
  }
}
.name {
  min-width: 200px;
  margin-right: 14px;
}

.price {
  min-width: 100px;
  margin-right: 14px;
  p {
    margin: 0;
    padding: 0;
    font-size: 14px;
  }
}

.header_line {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding: 8px 20px;
  width: 100%;
  margin-bottom: 12px;

  h6 {
    //min-width: 220px;
