@import "../../../assets/colors";

$CONTAINER_WIDTH: 250px;

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  //width: 100%;
  cursor: pointer;
  padding: 10px;
  width: $CONTAINER_WIDTH;
  margin: 16px 0;
}

.row_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  cursor: pointer;
  //padding: 0 10px;
  //width: $CONTAINER_WIDTH;
  margin: 8px 0;
}

.image_container {
  width: 210px;
  max-width: 90vw;
  //height: 210px;
  max-height: 90vw;

  overflow: hidden;
  border-radius: 6px;
  margin-bottom: 10px;
  //background-color: #58FFB8;
}

.default_image {
  width: 100%;
  height: 100%;
  min-height: 210px;
  border-radius: 6px;
  border: dashed 2px $BLUE_200;
  background-color: $BLUE_100;
}

.text_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  padding: 0 8px;
  //background-color: #e26163;
}

.price {
  font-weight: bold;
}

.description {
  font-size: 14px;
  line-height: 19px;
}