footer {
    margin-top: 80px;
}

.social {
    width: 100%;
    background: #797df6;
    border-radius: 25px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    @media (max-width: 800px) {
        flex-direction: column;
        align-items: center;
        width: auto;
        padding: 20px 15px 35.79px;
    }
}

.title {
    margin-top: 30px;
    font-size: 32px;
    line-height: 150%;
    color: #FFFFFF;

    @media (max-width: 800px) {
        font-size: 24px;
        line-height: 150%;
        margin: 0;
        width: 290px;
    }
}

.boldtitle {
    font-size: 38px;
    font-family: "Inter-Bold";
}

.reg {
    padding-bottom: 48px;
    padding-top: 24px;
    display: flex;
    gap: 20px;
    
    @media (max-width: 800px) {
        flex-direction: column;
        gap: 10px;
        padding-top: 20px;
        padding-bottom: 25px;
    }
}

.bt {
    background: #141414;
    border-radius: 8px;
    padding: 16px 36px 17px;
    border: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    align-items: center;
    cursor: pointer;
}

.in {
    width: 169px;
    border-radius: 8px;
    border: none;
    font-size: 16px;
    line-height: 19px;
    padding: 23px 16px 10px 14px;

    @media (max-width: 800px) {
        width: auto;
    }
}

.phone {
    position: absolute;
    font-size: 12px;
    line-height: 15px;
    color: #7F8A9B;
    margin-left: 14px;
    margin-top: 6px;
}

.description {
    font-family: "Inter-Bold";
    font-size: 18px;
    line-height: 150%;
    color: #FFFFFF;
}

.icons {
    display: flex;
    gap: 27px;
    padding: 0;
}

.icon {
    width: 40px;
    height: 40px;
    cursor: pointer;
}
.vk {
    background: url(../img/svg/vk.svg);
}

.google {
    background: url(../img/svg/google.svg);
}

.ya {
    background: url(../img/svg/ya.svg);
}

.right {
    width: 278px;
}

.picture {
    position: absolute;
    background: url(../img/svg/chain.svg);
    background-size: contain;
    width: 280px;
    height: 234px;
    margin-top: -136px;

    @media (max-width: 800px) {
        background: url(../img/svg/chain.png);
        width: 135px;
        height: 170.69px;
        margin-top: -424px;
        right: 0px;
    }
}

.copi {
    width: 100%;
    margin: 50px auto;
    font-size: 16px;
    line-height: 19px;
    color: #626D7D;
    display: flex;
    justify-content: space-between;

    @media (max-width: 800px) {
        width: auto;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        margin: 40px auto;
    }
}