.rec.rec-arrow {
    background: none;
    color: #6266EB;
    cursor: pointer;
    box-shadow: none;
    width: 20px;


    &:hover {
        opacity: 0.7;
    }

    @media (max-width: 800px) {
        display: none;
    }
}

.rec.rec-arrow:hover {
    background: none;
    box-shadow: none;
    color: #6266EB;
}

.rec.rec-arrow:disabled {
    visibility: hidden;
}

.rec.rec-arrow:enabled {
    background: none;
    box-shadow: none;
    color: #6266EB;
}

.rec-carousel-item:focus {
    background: none;
    box-shadow: none;
    color: #6266EB;
}

.kXteup:hover:enabled,
.kXteup:focus:enabled {
    color: #6266EB !important;
    background: none !important;
    box-shadow: none !important;
}

.rec.rec-slider-container {
    margin: 0;
}