@import "../../../assets/colors";
@import "../../../assets/fonts";

$elem_height: 45px;

.main_div {
  cursor: pointer;
  min-width: 200px;
  max-width: 350px;
  min-height: $elem_height;
  max-height: $elem_height;
  position: relative;
  //background-color: #deffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;

  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.8);
  //background-color: $BLUE_200;
  font-weight: bold;
  color: $BLUE_500;
  backdrop-filter: blur(10px);
  //border-style: solid;
  border-radius: 10px;
  //border-color: $BLUE_200;
  //border-width: 1px;
  z-index: 10001;
}

.main_relative {
  position: relative;
  //padding-top: 4px;
  //margin-bottom: 0;
  //padding-bottom: 18px;
}

.list_block {
  //background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  top: $elem_height + 10px - 18px;
  left: 0;
  right: 0;
  position: absolute;
  max-height: 200px;
  overflow: auto;
  transition: all 0.2s ease-in-out;
  background-color: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  border-radius: 10px;
  border-style: solid;
  border-color: $BLUE_500;
  border-width: 1px;
  z-index: 13000;
  //transform: scale(1);
  //animation: showUp 1s;
}

.not_shown_list {
  //display: none;
  max-height: 0;
  overflow: hidden;
  //animation: hid 1s;
  //transform: scaleY(0);
  opacity: 0;
}

