.list_block::-webkit-scrollbar {
  width: 7px;
  background-color: rgba(0, 0, 0, 0);
}

.list_block::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}

.main_block {
  cursor: pointer;
  min-height: $elem_height;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  width: 100%;
  z-index: 10001;
  //background-color: #FFB49C;
  //position: absolute;
}

.main_inactive {
  background-color: $GRAY_200;
  color: $GRAY_400;
  cursor: default;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
}

.main_element {
  //background-color: #58FFB8;
  //cursor: pointer;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
  font-family: $openSans;
  height: 100%;
  //position: absolute;
}

.list_element {
  //background-color: #FFB49C;
  cursor: pointer;
  padding-left: 10px;
  padding-right: 10px;
  min-height: $elem_height;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  transition: background-color 0.2s ease;
  font-size: 14px;
  font-family: $openSans;
  border-radius: 10px;
  color: black;


  &:hover {
    background-color: rgba(74, 87, 255, 0.08);
  }
}