@import "../../../assets/fonts";

.role_elem {
  min-height: 50px;
  min-width: 200px;
  background-color: rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-bottom: 5px;
  border-radius: 10px;

  font-size: 18px;

  transition: background-color 0.5s;

  &:hover {
    background-color: rgba(0, 0, 255, 0.1);
  }
}

.role_active {
  background-color: rgba(0, 0, 255, 0.1);
}

.roles_list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  border-style: solid;
  border-radius: 20px;
  border-color: $BLUE_300;
  border-width: 1px;
  padding: 10px;
}

