
.container {
  width: 100%;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  padding: 0 10px;

  button {
    margin-left: 16px;
    min-width: 100px;
  }
}

.search_option {
  width: 100%;
}

.search_input {
  width: 100%;
}

.main_container {
  width: 100%;

  h4 {
    margin: 30px 30px;
    text-align: center;
  }
}

.products_container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
}

.button_add_container {
  margin: 16px 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}