.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100vh;
  //background-color: #b6a958;
  padding: 10px;
  //position: fixed;
}

.up_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
