$margin_stat_elem_hor: 4%;
$padding_stat_elem: 5%;

.left_menu_item {
  width: 100% - $padding_stat_elem;
  min-height: 45px;
  //height: 200px;
  margin: 1.5% 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0);
  padding-left: $padding_stat_elem;

  font-weight: bold;
  font-family: $openSans;
  font-size: 16px;

  transition: all 0.2s;
  cursor: pointer;

  border-radius: 0 20px 20px 0;
  color: $GRAY_400;

  &:hover {
    //box-shadow: 0 3px 5px rgba(0, 0, 0, 0.5);
    background-color: rgba(0, 0, 0, 0.03);
    //color: $ORANGE_500;
  }
}

.active_left_menu_item {
  color: black;
  //border-radius: 0 20px 20px 0;
  background-color: rgba(182, 187, 255, 0.08);

  &:hover {
    background-color: rgba(74, 87, 255, 0.08);
  }
}


.header_inner_block {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.header_inner_block_info {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  align-content: stretch;
  width: 100%;
  padding-left: 24px;
  margin-bottom: 6px;
  //background-color: #58FFB8;
}


.header_back_info {
  display: flex;
  //flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  align-content: stretch;
  cursor: pointer;
  border-radius: 6px;
  padding-right: 20px;
  //height: 100%;

  p {
    color: $GRAY_400;
    padding: 0;
    margin: 0;
  }

  &:hover {
    background: linear-gradient(to right, $GRAY_200, rgba(255, 255, 255, 0),);

    p {
      color: black;
    }
  }
}


.header_inner_block_titles {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  //width: 100%;
  padding-left: 24px;
  //padding-bottom: 8px;
  //background-color: #e26163;

  h6 {
    //margin: 0;
    margin-bottom: 2px;
    font-size: 18px;
    padding: 0;
  }

  p {
    font-size: 14px;
    color: $GRAY_400;
    padding: 0;
    margin: 0;
  }
}


.header_inner_block_menu_1 {
  display: flex;
