  //background-color: $GRAY_50;
  padding: 10px 0;
}

.header_inner_block_menu_item {
  //min-width: 15%;
  padding: 0 16px;
  cursor: pointer;

  h6 {
    margin: 0;
    padding: 0;
    font-size: 16px;
    color: $GRAY_400;
  }

  &:hover {
    h6 {
      color: black;
    }
  }
}

.header_inner_block_menu_item_active {
  //background-color: black;
  h6 {
    color: black;
  }
}


@media screen and (max-width: $TABLET_WIDTH) {
  .header__inner {
