}


.attention_block {
  border-radius: 6px;
  border: solid 2px $BLUE_500;
  background-color: white;
  padding: 10px;
  margin: 20px 5%;
  width: 90%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  p {
