@import "../../../../assets/colors";
@import "../../../../assets/fonts";

.form {
  max-width: 100vw;
  background-color: rgba(0, 0, 0, 0);
  h4 {
    text-align: center;
  }
  h6 {
    margin: 32px 0;
    margin-top: 50px;
    color: $GRAY_400;
    font-size: 16px;
    padding-bottom: 10px;
    border-bottom: solid 1px $GRAY_300;
  }
}

$IMAGE_SIDE: 180px;
$CLOSE_IMAGE_SIDE: 30px;

.images {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax($IMAGE_SIDE, 1fr));
}

.delete_button {
  display: none;
  position: absolute;
  cursor: pointer;

  right: 0;
  top: 0;
  height: $CLOSE_IMAGE_SIDE;
  width: $CLOSE_IMAGE_SIDE;
  border-radius: 4px;
  background-color: $RED_400;
  align-items: center;
  justify-content: center;
  padding: 6px;

  &:hover {
    background-color: $RED_500;
  }
}

.do_main_button, .main_picture {
  display: flex;
  position: absolute;
  //cursor: pointer;

  left: 0;
  top: 0;
  padding: 6px;
  height: 30px;
  max-width: $IMAGE_SIDE - $CLOSE_IMAGE_SIDE - 40px;
  border-radius: 4px;
  background-color: $BLUE_400;
  color: white;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  p {
    color: white;
    font-size: 10px;
    padding: 0;
    margin: 0;
  }
}

.edit_picture {
  display: none;
  position: absolute;
  cursor: pointer;

  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  //padding: 6px;
  //height: 30px;
  //max-width: $IMAGE_SIDE - $CLOSE_IMAGE_SIDE - 40px;
  //border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition-duration: 0.3s;

  p {
    color: white;
    font-size: 18px;
    padding: 0;
    margin: 0;
  }
}

.do_main_button {
  display: none;
  cursor: pointer;

  &:hover {
    background-color: $BLUE_500;
  }
}

.close_img {
  width: $CLOSE_IMAGE_SIDE - 10px;
  height: $CLOSE_IMAGE_SIDE - 10px;
  //margin-right: 8px;
  //background-color: #AD009F;
  //background: #fff url("../../assets/Landing/svg_icons/mark.svg") no-repeat center;
  //color: #AD009F;
  //-webkit-filter: invert(100%);
}

.image_container {
  position: relative;
  width: $IMAGE_SIDE;
  height: $IMAGE_SIDE;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  border-radius: 6px;
  background-color: white;
  border: solid 1px #bebebe;
  padding: 2px;
  margin: 3px;

  //.main_picture {
  //  display: flex;
  //}

  &:hover {
    //transform: scale(1.5);

    .delete_button, .do_main_button, .edit_picture {
      display: flex;
    }
  }
}

.upload_container {
  margin: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  p {
    color: $BLUE_500;
    font-size: 12px;
    padding: 0;
    margin: 0;
    line-height: 17px;
    min-width: 30vw;
  }
}

.error {
  color: $RED_500;
}

.upload_label {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 80px;
  max-width: 300px;
  min-height: 40px;
  padding: 4px 16px;
  margin-right: 20px;
  //box-shadow: 0 2px 2px rgba(0,0,0,0.5);

  background: $BLUE_400;
  border-radius: 6px;

  outline: none;
  color: white;

  text-align: center;

  font-family: $openSans;
  font-size: 14px;

  font-weight: bold;
  transition: all 0.2s;
  cursor: pointer;
  &:hover {
    color: white;
    background: $BLUE_500;
    //border-color: $GRAY_500;
  }
  &:active {
    color: white;
    background: $GRAY_500;
    //border-color: $GRAY_500;
    //background: $BLUE_300;
    //border-color: $BLUE_300;
    //box-shadow: 0 0 3px rgba(0,0,0,0.3);
  }
}

.main_picture {
  //display: flex;
}

//.image {
//  width: 100%;
//}

$FILE_ICO_SIDE: 24px;

.document_ico, .download_ico, .delete_file_ico {
  width: $FILE_ICO_SIDE;
  height: $FILE_ICO_SIDE;
  //margin-right: 8px;
  //background-color: #AD009F;
  //background: #fff url("../../assets/Landing/svg_icons/mark.svg") no-repeat center;
  //color: #AD009F;
  //-webkit-filter: invert(100%);
}

.document_ico {
  -webkit-filter: invert(100%);
}

$FILE_BORDER_RADIUS: 6px;
$FILE_CONTAINER_WIDTH: 270px;

.file_download_container {
  display: none;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: $FILE_CONTAINER_WIDTH / 2;
  padding: 0 15px;
  //flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0.95);
  border-top-left-radius: $FILE_BORDER_RADIUS;
  border-bottom-left-radius: $FILE_BORDER_RADIUS;
  border-left: solid 1px $GRAY_500;
  border-top: solid 1px $GRAY_500;
  border-bottom: solid 1px $GRAY_500;

  &:hover {
    background-color: $GRAY_200;
  }
}

.file_delete_container {
  display: none;
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 0;
  bottom: 0;
  width: $FILE_CONTAINER_WIDTH / 2;
  padding: 0 15px;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0.95);
  border-top-right-radius: $FILE_BORDER_RADIUS;
  border-bottom-right-radius: $FILE_BORDER_RADIUS;
  border-right: solid 1px $RED_500;
  border-top: solid 1px $RED_500;
  border-bottom: solid 1px $RED_500;

  &:hover {
    background-color: $RED_200;
  }
}

.file_container {
  position: relative;
  overflow: hidden;
  width: $FILE_CONTAINER_WIDTH;
  min-height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  border-radius: $FILE_BORDER_RADIUS;
  background-color: $BLUE_500;
  //border: solid 1px #bebebe;
  padding: 4px;
  margin: 3px;
  color: white;

  //.main_picture {
  //  display: flex;
  //}

  p {
    width: 100%;
    color: white;
    font-size: 12px;
    line-height: 16px;
    padding-left: 8px;
    text-align: left;
    margin: 0;
    //background-color: #58FFB8;
  }

  &:hover {
    //transform: scale(1.5);

    .file_download_container, .file_delete_container {
      display: flex;
    }
    .file_download_container {
      p {
        color: $GRAY_500;
        font-weight: bold;
      }
    }

    .file_delete_container {
      p {
        color: $RED_500;
        font-weight: bold;
      }
    }
  }
}

.files {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax($FILE_CONTAINER_WIDTH + 16px, 1fr));
}

$FIELD_TRASH_CONTAINER_SIDE: 40px;

.field_trash {
  display: flex;
  cursor: pointer;

  margin-top: 12px;
  margin-left: 12px;

  height: $FIELD_TRASH_CONTAINER_SIDE;
  width: $FIELD_TRASH_CONTAINER_SIDE;
  border-radius: 4px;
  background-color: $RED_400;
  align-items: center;
  justify-content: center;
  padding: 4px;

  &:hover {
    background-color: $RED_500;
  }
}

.field_trash_ico {
  width: $FIELD_TRASH_CONTAINER_SIDE;
  height: $FIELD_TRASH_CONTAINER_SIDE;
  //-webkit-filter: invert(100%);
}


.field_container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}